import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
//component/* import { AddChemistComponent } from './add-chemist.component';
// import routingmodule import { AddChemistRoutingModule } from './add-chemist-routing.module'; */
import { SharedModule } from '../../../shared/shared.module';
import { ChartModule } from 'angular2-chartjs';
import { SimpleNotificationsModule } from 'angular2-notifications';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import '../../../../assets/jquery-locationpicker-plugin-master/src/locationpicker.jquery.js';
import { ProfileComponent } from './profile.component';
import { ProfileRoutingModule } from './profile-routing.module';

@NgModule({
  imports: [
    CommonModule,
    ProfileRoutingModule,
    SharedModule,
    ChartModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    SimpleNotificationsModule.forRoot()
  ],
  declarations: [ProfileComponent],
  bootstrap: [ProfileComponent]
})
export class ProfileModule { }



